import React, { useState, useEffect, useContext } from "react";
import "semantic-ui-css/semantic.min.css";
import "./App.css";
import { Carrito } from "./Contexto";
import {
  Container,
  Segment,
  Header,
  Confirm,
  Table,
  List,
  Popup,
  Image,
  Label,
  Button,
  Icon,
  Modal,
  Dimmer,
  Loader,
  Input,
  Dropdown,
} from "semantic-ui-react";
import clientes from "./clientes.json";
import { GoogleSpreadsheet } from "google-spreadsheet";

function useForceUpdate() {
  const [value, setValue] = useState(0); // integer state
  return () => setValue((value) => value + 1); // update the state to force render
}

function removeDuplicates(myArr, prop) {
  return myArr.filter((obj, pos, arr) => {
    return arr.map((mapObj) => mapObj[prop]).indexOf(obj[prop]) === pos;
  });
}

function currencyFormat(num) {
  return "$" + num.toFixed(2).replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1,");
}

export default function Cart() {
  //fechas
  let today = new Date();
  var dd = today.getDate();
  var mm = today.getMonth() + 1;
  var yyyy = today.getFullYear();
  var hora = today.getHours();
  var minutos = today.getMinutes();
  dd < 10 ? (dd = "0" + dd) : (dd = today.getDate());
  mm < 10 ? (mm = "0" + mm) : (mm = today.getMonth() + 1);
  if (hora < 10) hora = "0" + hora;
  if (minutos < 10) minutos = "0" + minutos;
  let date = yyyy + "-" + mm + "-" + dd;
  let fecha = date + " " + hora + ":" + minutos;
  //fechas
  const {
    ["articulos"]: [articulos, setArticulos],
  } = useContext(Carrito);
  const {
    ["cantidades"]: [cantidades, setCantidades],
  } = useContext(Carrito);
  const forceUpdate = useForceUpdate();
  const [loading, setLoading] = useState(false);
  const [cliente, setCliente] = useState();
  const [newCliente, setNewCliente] = useState(false);
  const [pago, setPago] = useState("Credito");
  const [ttlamt, setTtlamt] = useState();
  const [payamt, setPayamt] = useState(0);
  const [pedido, setPedido] = useState("Retiro");
  const [saving, setSaving] = useState(false);
  const [data, setData] = useState();
  const [open, setOpen] = useState(false);
  const [activeItem, setActiveItem] = useState("");
  const [listaClientes, setListaClientes] = useState([]);
  const [confirmar, setConfirmar] = useState(false);
  const [confirmado, setConfirmado] = useState(false);
  const sacardata = async () => {
    setLoading(true);
    var data = [];
    let datas = [];
    const creds = require("./samsnacks.json"); // the file saved above
    const doc = new GoogleSpreadsheet(
      "1usbvWq_DBOLN4j1wvK-l7QquTHe9yc9PqFRXhhOmZk0"
    );
    await doc.useServiceAccountAuth(creds);
    await doc.loadInfo(); // loads document properties and worksheets
    const sheet = doc.sheetsById[0];
    const rows = await sheet.getRows();
    const cols = await sheet.columnCount;
    const encabezado = await sheet.headerValues;
    let lastr = rows.length + 1;
    var j, i;
    await sheet.loadCells({
      // GridRange object
      startRowIndex: 0,
      endRowIndex: lastr,
      startColumnIndex: 0,
      endColumnIndex: cols,
    });
    for (j = 1; j < lastr; j++) {
      data = [];
      for (i = 0; i <= cols - 1; i++) {
        window["Celda" + j + i] = sheet.getCell(j, i).value;
        data[`${encabezado[i]}`] = window["Celda" + j + i];
      }
      datas.push(data);
    }
    setData(datas);
    console.log(datas);
    setLoading(false);
  };

  useEffect(() => {
    !data && sacardata();
    if (data) {
      var temp = removeDuplicates(data, "Cliente");
      let templista = [];
      templista.push({ key: "new", value: "new", text: "new" });
      var i;
      for (i = 0; i < temp.length; i++) {
        templista.push({
          key: temp[i].Cliente,
          value: temp[i].Cliente,
          text: temp[i].Cliente,
        });
      }
      setListaClientes(templista);
      console.log(temp);
    }
  }, [data]);

  useEffect(() => {
    console.log(listaClientes);
  }, [listaClientes]);

  useEffect(() => {
    if (articulos) {
      var i;
      for (i = 0; i < articulos.length; i++) {
        let result = articulos.reduce((r, d) => r + d.amt, 0);
        setTtlamt(result);
      }
    }
  }, [articulos]);

  function QuitarArt(name) {
    let updatedArticulos = articulos.filter((item) => {
      return item.name !== name;
    });
    setArticulos(updatedArticulos);
    setCantidades(updatedArticulos.length);
    var i;
    for (i = 0; i < updatedArticulos.length; i++) {
      let result = updatedArticulos.reduce((r, d) => r + d.amt, 0);
      setTtlamt(result);
    }
    if (updatedArticulos.length === 0) {
      setTtlamt(0);
    }
  }

  function ActCart(name, cant, price) {
    articulos.forEach(function (obj) {
      if (obj.name === name) {
        console.log(obj.name);
        obj.cant = cant;
        obj.amt = cant * price;
      }
    });
    var i;
    for (i = 0; i < articulos.length; i++) {
      let result = articulos.reduce((r, d) => r + d.amt, 0);
      setTtlamt(result);
    }
  }

  const registrar = async () => {
    if (!cliente) {
      alert("Escriba su Nombre Completo");
    } else {
      setSaving(true);
      const creds = require("./samsnacks.json"); // the file saved above
      const doc = new GoogleSpreadsheet(
        "1usbvWq_DBOLN4j1wvK-l7QquTHe9yc9PqFRXhhOmZk0"
      );
      await doc.useServiceAccountAuth(creds);
      await doc.loadInfo(); // loads document properties and worksheets
      const sheet = doc.sheetsById[0];
      const pedidoid = Date.now() + cliente.split(" ").join("");
      var i;
      var applyamt;
      var pay = parseFloat(payamt);
      var pend = parseFloat(payamt);
      console.log(pend);
      for (i = 0; i < articulos.length; i++) {
        applyamt = 0;
        if (ttlamt > pay) {
          console.log("cond1", i, articulos.length, articulos[i].amt, pend);
          if (articulos[i].amt <= pend) {
            applyamt = articulos[i].amt;
            pend = pend - articulos[i].amt;
          } else {
            applyamt = pend;
          }
        } else {
          if (i === articulos.length - 1) {
            console.log("cond2.1", i, articulos.length, articulos[i].amt, pend);
            applyamt = pend;
          } else {
            console.log("cond2.2", i, articulos.length, articulos[i].amt, pend);
            applyamt = articulos[i].amt;
            pend = pend - articulos[i].amt;
          }
        }
        const newData = await sheet.addRow({
          Fecha: fecha,
          Cliente: cliente,
          Product_Id: articulos[i].id,
          Product_Name: articulos[i].name,
          Cantidad: articulos[i].cant,
          Monto: articulos[i].amt,
          Payment_Method: pago,
          Estatus: "Pedido",
          Entrega: pedido,
          //Tel: tel,
          NumPedido: pedidoid,
          Payment_Amt: applyamt,
        });
      }
      alert(`Pedido Realizado, su numero de pedido es ${pedidoid}`);
      setCantidades(0);
      setArticulos([]);
      setTtlamt();
      setCliente();
      setSaving(false);
    }
  };

  useEffect(() => {
    activeItem && console.log(activeItem);
  }, [activeItem]);

  return (
    <Container>
      <Dimmer active={saving || loading} page>
        <Loader>{saving ? "Saving" : "Loading"}</Loader>
      </Dimmer>
      <Confirm
        header={"Deseas Eliminar " + activeItem.name}
        content=""
        cancelButton="Cancelar"
        confirmButton="Confirmar"
        open={confirmar}
        onCancel={() => {
          setConfirmado(false);
          setConfirmar(false);
        }}
        onConfirm={() => {
          setConfirmado(true);
          QuitarArt(activeItem.name);
          setConfirmar(false);
        }}
      />
      {activeItem && (
        <Modal
          closeOnEscape={false}
          closeOnDimmerClick={false}
          onClose={() => setOpen(false)}
          onOpen={() => setOpen(true)}
          open={open}
          size="small"
        >
          <Modal.Header>{activeItem.name}</Modal.Header>
          <Modal.Content>
            <Image size="small" src={activeItem.src}></Image>
            <Modal.Description>
              <p>
                {activeItem.price
                  ? currencyFormat(activeItem.price)
                  : activeItem.price}
              </p>
              <p>
                <Input
                  type="number"
                  min="0"
                  defaultValue={activeItem.cant}
                  onChange={(e, { value }) => {
                    setActiveItem((prevObject) => ({
                      ...prevObject,
                      name: activeItem.name,
                      price: activeItem.price,
                      src: activeItem.src,
                      id: activeItem.id,
                      cant: value,
                      amt: activeItem.price * value,
                    }));
                  }}
                ></Input>
              </p>
            </Modal.Description>
          </Modal.Content>
          <Modal.Actions>
            <Button
              //disabled={activeItem.disponible <= 0}
              color="green"
              onClick={() => {
                ActCart(activeItem.name, activeItem.cant, activeItem.price);
                setOpen(false);
              }}
            >
              Actualizar
            </Button>
            <Button
              color="red"
              onClick={() => {
                setOpen(false);
              }}
            >
              Cancelar
            </Button>
          </Modal.Actions>
        </Modal>
      )}
      <Header>Finalizar Compra</Header>
      <Segment>
        {newCliente ? (
          <Input
            placeholder="Nombre Completo"
            fluid
            onChange={(e, { value }) => {
              setCliente(value);
            }}
          ></Input>
        ) : (
          <Dropdown
            placeholder="Nombre Completo"
            fluid
            search
            selection
            options={listaClientes}
            onChange={(e, { value }) => {
              value === "new" ? setNewCliente(true) : setCliente(value);
            }}
          />
        )}
        <br />
        <p style={{ display: "inline-block" }}>
          <Popup
            content="Credito"
            trigger={
              <Button
                icon
                active={pago === "Credito"}
                color={pago === "Credito" ? "red" : "white"}
                onClick={() => {
                  setPago("Credito");
                  setPayamt(0);
                }}
              >
                <Icon name="thumbs down outline" />
              </Button>
            }
          ></Popup>
          <Popup
            trigger={
              <Button
                icon
                active={pago === "Contado"}
                color={pago === "Contado" ? "green" : "white"}
                onClick={() => {
                  setPago("Contado");
                  setPayamt(ttlamt);
                }}
              >
                <Icon name="money bill alternate outline" />
              </Button>
            }
            content="Contado"
          ></Popup>
          {pago === "Contado" && (
            <Input
              step="0.05"
              type="number"
              style={{ width: "30%" }}
              defaultValue={payamt}
              onChange={(e, { value }) => {
                setPayamt(value);
              }}
            ></Input>
          )}

          <Popup
            trigger={
              <Button
                icon
                active
                color={pedido === "Retiro" ? "green" : "red"}
                onClick={() => {
                  setPedido(pedido === "Retiro" ? "Domicilio" : "Retiro");
                }}
              >
                <Icon name={pedido === "Retiro" ? "user" : "truck"} />
              </Button>
            }
            content={pedido}
          ></Popup>
          <Button
            color="green"
            disabled={!cliente || articulos.length === 0}
            onClick={() => {
              registrar();
            }}
          >
            SAVE
          </Button>
          <h2>{ttlamt && currencyFormat(ttlamt)}</h2>
        </p>
      </Segment>
      {!articulos && (
        <Segment style={{ height: "40vh", overflowY: "scroll" }}>
          {articulos.map((item) => (
            <div style={{ display: "inline-block", width: "40vw" }}>
              <Header>{item.name}</Header>

              <Input
                type="number"
                min="0"
                style={{ width: "30%" }}
                defaultValue={item.cant}
                onChange={(e, { value }) => {
                  ActCart(item.name, value, item.price);
                  forceUpdate();
                }}
              ></Input>
              <br />
              <Input style={{ width: "15%" }} disabled>
                {currencyFormat(item.amt)}
              </Input>
              <a
                onClick={() => {
                  QuitarArt(item.name);
                }}
              >
                Quitar
              </a>
            </div>
          ))}
        </Segment>
      )}
      {articulos && (
        <Segment style={{ height: "40vh", overflowY: "scroll" }}>
          <Table basic="very" celled collapsing>
            <Table.Header>
              <Table.Row>
                <Table.HeaderCell>Producto</Table.HeaderCell>
                <Table.HeaderCell>Cantidad</Table.HeaderCell>
                <Table.HeaderCell>Monto</Table.HeaderCell>
              </Table.Row>
            </Table.Header>

            <Table.Body>
              {articulos.map((item) => (
                <Table.Row>
                  <Table.Cell>
                    <Header as="h4" image>
                      <Image src={item.src} rounded size="mini" />
                      <Header.Content>
                        {item.name}
                        <Header.Subheader>
                          {currencyFormat(item.price)}
                        </Header.Subheader>
                      </Header.Content>
                    </Header>
                  </Table.Cell>
                  <Table.Cell>{item.cant}</Table.Cell>
                  <Table.Cell>{currencyFormat(item.amt)}</Table.Cell>
                  <Table.Cell>
                    <a
                      href="#"
                      onClick={() => {
                        setActiveItem((prevObject) => ({
                          ...prevObject,
                          name: item.name,
                          price: item.price,
                          src: item.src,
                          id: item.id,
                          cant: item.cant,
                          amt: item.amt,
                        }));
                        setOpen(true);
                      }}
                    >
                      Editar
                    </a>
                  </Table.Cell>
                  <Table.Cell>
                    <a
                      href="#"
                      onClick={() => {
                        setActiveItem((prevObject) => ({
                          ...prevObject,
                          name: item.name,
                          price: item.price,
                          src: item.src,
                          id: item.id,
                          cant: item.cant,
                          amt: item.amt,
                        }));
                        setConfirmar(true);
                      }}
                    >
                      Quitar
                    </a>
                  </Table.Cell>
                </Table.Row>
              ))}
              <Table.Cell>
                <b>TOTAL</b>
              </Table.Cell>
              <Table.Cell></Table.Cell>
              <Table.Cell>
                <b>{ttlamt && currencyFormat(ttlamt)}</b>
              </Table.Cell>
              <Table.Cell></Table.Cell>
            </Table.Body>
          </Table>
        </Segment>
      )}
    </Container>
  );
}
